.projects-main{
    width: 99vw;
    height: 100vh;
}

.project-text{
    display: flex;
    justify-content: center;
    font-size: 3vh;
}
.projects-scroller{
    width: 30vw;
    height: 95vh;
    overflow-y: hidden;
}
.scroller{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100vw;
}

.img-project{ 
    border: 2px solid #219EBC;
    
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 5px;
    padding: 5px;

}

.img-project:hover{
    cursor: pointer;
}


.img-project img{
    width: 100%;
}

.text{
    margin: 5px;
    font-size: 2vh;
}

.proj-title{
    color:#132561 ;
}

img{
    width: 20vw;

    /* margin: 5px; */
}